@import "https://fonts.googleapis.com/css2?family=Comfortaa&display=swap";
body {
  background-color: #000;
  margin: 0;
  font-family: Comfortaa, sans-serif;
  overflow: hidden;
}

.guiContainer {
  background-color: #0000001a;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

.guiText {
  color: #0000;
  text-align: center;
  max-width: 12em;
  padding: 10px;
  animation: .25s linear .25s forwards fadeIn;
}

p {
  font-size: .7em;
}

h6 {
  margin: 0;
  font-size: 2em;
}

@keyframes fadeIn {
  0% {
    color: #fff0;
    background-color: #0000;
  }

  100% {
    color: #ffffffe6;
    background-color: #0009;
  }
}

@media only screen and (width <= 1000px) {
  h6 {
    font-size: 3em;
  }

  p {
    font-size: 1.5em;
  }
}
